import React, { useState, useEffect } from 'react';
import './OptimizedImage.css';

const OptimizedImage = ({ src, alt, className, loading = 'lazy', onClick, ...props }) => {
  const [imgSrc, setImgSrc] = useState(() => {
    if (src && /\.(jpg|jpeg|png)$/i.test(src)) {
      return src.replace(/\.(jpg|jpeg|png)$/i, '.webp');
    }
    return src;
  });
  const [hasError, setHasError] = useState(false);

  // Update imgSrc when src prop changes
  useEffect(() => {
    if (src && /\.(jpg|jpeg|png)$/i.test(src)) {
      setImgSrc(src.replace(/\.(jpg|jpeg|png)$/i, '.webp'));
    } else {
      setImgSrc(src);
    }
    setHasError(false);
  }, [src]);

  const handleError = () => {
    if (imgSrc.includes('.webp') && !hasError) {
      setImgSrc(src); // Fallback to original format
      setHasError(true);
    }
  };

  return (
    <img
      src={imgSrc}
      alt={alt}
      className={className}
      loading={loading}
      onError={handleError}
      onClick={onClick}
      {...props}
    />
  );
};

export default OptimizedImage;